import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./micro-lector.module.scss"
import PageContentSeparator from "../../components/page-content-separator"
import ParagraphSeparator from "../../components/paragraph-separator"
import microLectorImage from "../../images/microLector.png"
import microLectorFontsImage from "../../images/microLectorFonts.png"

export default () => {
  const { mockupMicroLector } = useStaticQuery(graphql`
    query {
      mockupMicroLector: file(relativePath: { eq: "mockupMicroLector.png" }) {
        childImageSharp {
          fixed(height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.pageHeadingSection}>
        <Img
          fixed={mockupMicroLector.childImageSharp.fixed}
          objectFit="cover"
          objectPosition="50% 50%"
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className={styles.pageMainContentSection}>
        <div className={styles.pageMainContent}>
          <PageContentSeparator />
          <div>
            <div className={styles.projectTitle}>MicroLector</div>
            <div className={styles.projectSubTitle}>
              Education Application Website
            </div>
          </div>
          <ParagraphSeparator />
          <div className={styles.projectText}>
            <div>
              This is the website of an education application -
              <strong> MicroLector </strong> by <strong> David Schreg </strong>
              (Switzerland). Link:&nbsp;
              <a className={styles.projectLink} href="https://microlector.com">
                https://microlector.com
              </a>
            </div>
            <ParagraphSeparator />
            <div>
              The original website is still simple and basic, therefore, I
              create an updated version for this existing website. Service logos
              and details, as well as attractive information (such as expert
              name and quote, special offer), have been added.
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Main color:</div>
              <div>
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox1}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox2}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox3}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox4}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.colorBox5}`}
                />
              </div>
            </div>
            <ParagraphSeparator />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Fonts:</div>
              <img src={microLectorFontsImage} alt="" />
            </div>
            <ParagraphSeparator />
            <div>Full page:</div>
            <ParagraphSeparator />
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <img src={microLectorImage} className={styles.projectImage} alt="" />
      </div>
      <PageContentSeparator />
    </React.Fragment>
  )
}
